  margin-right: 10px; /* Отступ справа от выпадающего меню */
}

/* Стили для селектора региона */
.region__selector {
  width: 100%; /* Ширина селектора */
  height: 35px; /* Высота селектора */
  border: 1px solid #ccc; /* Граница селектора */
  border-radius: 4px; /* Радиус границы селектора */
  padding: 5px; /* Отступы внутри селектора */
  font-size: 16px; /* Размер шрифта */
  cursor: pointer; /* Изменение курсора при наведении */
  -webkit-appearance: none; /* Удаление стандартного внешнего вида селектора в Safari */ /* Удаление стандартного внешнего вида селектора в Firefox */
  appearance: none; /* Удаление стандартного внешнего вида селектора в остальных браузерах */
}

/* Стили для стрелочки выпадающего меню */
.select-arrow {
  position: absolute;
  top: 50%;
  right: 10px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 0;
  height: 0;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 6px solid rgba(11, 99, 246, 1); /* Цвет стрелочки */
}

