.export-btn {
  cursor: pointer;
  width: 200px; /* Ширина кнопки */
  height: 35px;
  background-color: rgba(11, 99, 246, 1);
  color: white;
  border-radius: 5px;
  border: none;
  box-shadow: 0 5px 15px 0 rgba(11, 99, 246, 1);
  transition: 0.5s;
  margin-right: 20px;
  margin-top: 4px;
  transition: background-color 0.5s; /* Плавное изменение цвета фона при наведении */
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s; /* Плавное изменение цвета фона при наведении */
}

.export-btn:hover {
  -webkit-transform: translate(0,-3px);
          transform: translate(0,-3px);
  box-shadow: 0 20px 40px 0 rgb(55, 128, 255);
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: #fefefe;
  padding: 20px;
  border-radius: 5px;
  color: #333;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
  cursor: pointer;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

